

import React, { useEffect } from 'react';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import PropTypes from 'prop-types'; //props  
import ReactGA from 'react-ga'
const PresentationalFirstSection = ({
    localCurrency,
    currencyRate,
    productPriceInKrw,
    additionalCost,
    productWeight,
    pgFeeRate,
    salesFeeRate,
    payoneerFeeRate,
    fspFeeRate,
    onChangeCountryCode,
    onChangeCurrencyRate,
    onChangeProductPriceInKrw,
    onChangeAdditionalCost,
    onChangeProductWeight,
    onChangePgFeeRate,
    onChangeSalesFeeRate,
    onChangePayoneerFeeRate,
    onChangeFspFeeRate,
    onCalculatePriceForIncome,
}) => {
    useEffect(() => {
        ReactGA.initialize("G-QZ96X68ZSY");
        ReactGA.set({ page: window.location.pathname });
        ReactGA.pageview(window.location.pathname + window.location.search);
    }, []);

    return (
        <section className="page-section masthead portfolio" id="userInputSection">
            <div className="container">
                <h2 className="page-section-heading text-uppercase text-secondary mb-0">1.     </h2>
                <div className="divider-custom">
                    <div className="divider-custom-line" style={{ maxWidth: "initial" }}></div>
                </div>
                <h3 className="text-secondary content-in-container"></h3>
                <div className="row justify-content-between">
                    <div className="col-lg-6 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="country"></label>
                            <OverlayTrigger
                                key="country-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="country-info">
                                          (   )
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <select title=" ." onChange={onChangeCountryCode} className="form-control selectpicker show-tick" id="country">
                                <option value="SG"
                                    data-icon="<img className='selectbox-image' src='images/flags/singapore.png' /><span className='badge badge-success text-dark'></span>">
                                    </option>
                                <option value="MY"
                                    data-content="<img className='selectbox-image' src='images/flags/malaysia.png'><span className='text-dark'></span>">
                                    </option>
                                <option disabled
                                    data-content="<img className='selectbox-image' src='images/flags/indonesia.png'><span className='text-dark'>()</span>">
                                    </option>
                                <option disabled
                                    data-content="<img className='selectbox-image' src='images/flags/taiwan.png'><span className='text-dark'>()</span>">
                                     </option>
                                <option disabled
                                    data-content="<img className='selectbox-image' src='images/flags/thailand.png'><span className='text-dark'>()</span>">
                                     </option>
                                <option disabled
                                    data-content="<img className='selectbox-image' src='images/flags/vietnam.png'><span className='text-dark'>()</span>">
                                     </option>
                                <option disabled
                                    data-content="<img className='selectbox-image' src='images/flags/philippines.png'><span className='text-dark'>()</span>">
                                     </option>
                            </select>

                        </div>
                    </div>
                    <div className="col-lg-5 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="currency"></label>
                            <OverlayTrigger
                                key="currency-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="currency-info">
                                          .             .
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={currencyRate} onChange={onChangeCurrencyRate} type="number" min="0" className="form-control" placeholder=" ." aria-label=" " aria-describedby="currency-addon" id="currency" />
                                <div className="input-group-append">
                                    <span className="input-group-text" type="number" min="0" id="currency-addon">(KRW)</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-6 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="productPrice"> ()</label>
                            <OverlayTrigger
                                key="productPrice-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="productPrice-info">
                                           
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={productPriceInKrw} onChange={onChangeProductPriceInKrw} type="number" min="0" step="10" className="form-control" placeholder="  ." aria-label="  " aria-describedby="productPrice-addon" id="productPrice" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="productPrice-addon">(KRW)</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-5 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="addtionalCost">  </label>
                            <OverlayTrigger
                                key="addtionalCost-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="addtionalCost-info">
                                             - ,   
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={additionalCost} onChange={onChangeAdditionalCost} type="number" min="0" step="10" className="form-control" placeholder=" ,  ,   ." aria-label=" ,  ,   " aria-describedby="addtionalCost-addon" id="addtionalCost" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="addtionalCost-addon">(KRW)</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-6 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="productWeight"> </label>
                            <OverlayTrigger
                                key="productWeight-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="productWeight-info">
                                               10g   
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={productWeight} onChange={onChangeProductWeight} type="number" min="0" max="30000" step="10" className="form-control" placeholder=" (10g) ." aria-label=" (10g) ." aria-describedby="productWeight-addon" id="productWeight" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="productWeight-addon">(g)</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <h3 className="mt-5 text-secondary content-in-container">  </h3>
                <div className="row justify-content-between">
                    <div className="col-lg-6 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="country"> PG </label>
                            <OverlayTrigger
                                key="productWeight-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="productWeight-info">
                                              PG() 
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={pgFeeRate} onChange={onChangePgFeeRate} type="number" min="0" max="100" step="0.01" className="form-control" placeholder="%  ." aria-label="%  ." aria-describedby="transactionFee-addon" id="transactionFee" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="transactionFee-addon">%</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-5 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="country"> </label>
                            <OverlayTrigger
                                key="productWeight-info" placement="top" overlay={
                                    <Tooltip id="productWeight-info">
                                               
                                    </Tooltip>
                                }>
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={salesFeeRate} onChange={onChangeSalesFeeRate} type="number" min="0" max="100" step="0.01" className="form-control" placeholder="%  ." aria-label="%  ." aria-describedby="salesFee-addon" id="salesFee" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="salesFee-addon">%</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-6 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="country"> </label>
                            <OverlayTrigger
                                key="productWeight-info" placement="top" overlay={
                                    <Tooltip id="productWeight-info">
                                              
                                    </Tooltip>
                                }>
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={payoneerFeeRate} onChange={onChangePayoneerFeeRate} type="number" min="0" max="100" step="0.01" className="form-control"
                                    placeholder="%  ." aria-label="%  ." aria-describedby="payoniaFee-addon" id="payoniaFee" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="payoniaFee-addon">%</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-5 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="country">FSP </label>
                            <OverlayTrigger
                                key="productWeight-info" placement="top" overlay={
                                    <Tooltip id="productWeight-info">
                                        Free Shipping / Coin Cashback     
                                    </Tooltip>
                                }>
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={fspFeeRate} onChange={onChangeFspFeeRate} type="number" min="0" max="100" step="0.01" className="form-control" placeholder="%  ." aria-label="%  ." aria-describedby="fspFee-addon" id="fspFee" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="fspFee-addon">%</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="mt-5 pr-6 col-12 text-right content-in-container">
                        <button type="button" className="btn btn-primary" onClick={onCalculatePriceForIncome}></button>
                    </div>
                </div>
            </div>
        </section >
    );
};



PresentationalFirstSection.propTypes = {
    currencyRate: PropTypes.number,
    productPriceWithLocalCurrency: PropTypes.number,
    additionalCost: PropTypes.number,
    productWeight: PropTypes.number,
    pgFee: PropTypes.number,
    salesFee: PropTypes.number,
    payoneerFee: PropTypes.number,
    fspFee: PropTypes.number
};

export default PresentationalFirstSection;




